import { FormControl, InputLabel, MenuItem, Select } from '@mui/material'
import { Box } from '@mui/system'
import React from 'react'

function SelectResults({ setNumOfBlogsPerPage, setpage }) {
    let paginationInfo = JSON.parse(localStorage.getItem('paginationInfo'))

    const menuItems = [1, 2, 3, 4, 5, 6]
    const [value, setvalue] = React.useState(paginationInfo?.blogsperpage || menuItems[2]);

    const handleChange = (e) => {
        setvalue(e.target.value);
        localStorage.setItem('paginationInfo',JSON.stringify({page:1,blogsperpage:e.target.value}))
        setNumOfBlogsPerPage(e.target.value);
        setpage(1)
    };
    return (
        <Box sx={{ minWidth: 160 }}>
            <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Blogs Per Page</InputLabel>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={value}
                    label="Blogs Per Page"
                    onChange={handleChange}
                >
                    {
                        menuItems.map(item => {
                            return <MenuItem key={item} value={item}>{item}</MenuItem>
                        })
                    }
                </Select>
            </FormControl>
        </Box>
    )
}

export default SelectResults
